<template>
<div class="Url-itemPage">
    <i-modal v-model="basicWordItemShow" :title="operation + ''" width="500" :closable="false" :mask-closable="false" :styles="{top: '20px'}" :scrollable="true">

        <div class="basicWords-itemPage">

            <i-form ref="formCustom" :model="formCustom" :rules="ruleCustom" :label-width="80">

                <div class="basicWords-itemPage-form">
                    <div class="basicWords-itemPage-form-main" v-for="(item, xindex) in formCustom.Items" :key="xindex">
                        <i-formItem :label="'' + (parseInt(xindex) +1)" :prop="'Items.' + xindex +'.value' + '.word'" :rules="ruleCustom.word">
                            <i-row class="basicWords-itemPage-form-main-word">
                                <i-input class="basicWords-itemPage-form-main-word-input" @on-change="searchWordStatus(xindex)" type="text" v-model="item.value.word" placeholder=""></i-input>
                                <i-button v-show="formCustom.uid === ''" class="basicWords-itemPage-form-main-status-delete" type="error" @click="handleRemove(xindex)"></i-button>
                            </i-row>
                        </i-formItem>

                        <i-formItem class="status" label=":" :prop="'Items.' + xindex +'.value' + '.status'" :rules="ruleCustom.status">
                            <i-row class="basicWords-itemPage-form-main-status">
                                <i-input class="xyz" :readonly="true" type="text" v-model="item.value.status" placeholder=""></i-input>
                            </i-row>
                        </i-formItem>

                        <!--
                        <i-formItem v-if="item.value.source.split(',').length === 1" class="source" label=":">
                            <i-row>
                                <p> {{item.value.source}}</p>
                            </i-row>
                        </i-formItem>

                        <i-formItem v-else class="source" label=":">
                            <i-row>
                                <p v-for="(item2, index) in item.value.source.split(',')" :key="index"> {{'(' + (parseInt(index)+1) + ') '   + item2}}</p>
                            </i-row>
                        </i-formItem>
                        -->
                        <i-formItem label="" :prop="'Items.' + xindex +'.value' + '.category'" :rules="ruleCustom.category">
                            <i-row class="basicWords-itemPage-form-row special">
                                <i-select class="Url-itemPage-form-category-select" multiple v-model="item.value.category" placeholder="">
                                    <i-option v-for="itemx in rawCategories" :value="itemx" :key="itemx">{{ itemx }}</i-option>
                                </i-select>
                            </i-row>
                        </i-formItem>

                    </div>
                </div>
                <i-formItem>
                    <i-row>
                        <i-col span="12">
                            <i-button v-show="formCustom.uid === ''" type="dashed" long @click="handleAdd()" icon="md-add"></i-button>
                        </i-col>
                    </i-row>
                </i-formItem>

            </i-form>

        </div>
        <div slot="footer">
            <i-button v-show="formCustom.uid !== ''" class="Url-itemPage-delete-button" type="error" size="default" @click="modalDelete(formCustom.uid)"></i-button>
            <i-button type="default" size="large" @click="modalCancel('formCustom')"></i-button>
            <i-button :disabled="this.finalCount === 0" type="primary" size="large" @click="modalOk('formCustom')"></i-button>
        </div>
    </i-modal>
</div>
</template>

<script>
import {
    mapState,
    mapMutations
} from 'vuex'
export default {
    name: 'basicWordItemPage',
    data() {
        const validateWord = (rule, value, callback) => {
            if (value === '') {
                callback(new Error(''));
            } else {
                callback();
            }
        };
        const validateStatus = (rule, value, callback) => {
            if (value.trim() !== '') {
                callback(new Error('""'));
            } else {
                callback();
            }

        };
        const validateCategory = (rule, value, callback) => {
            if (value.length === 0) {
                console.log('category empty')
                callback(new Error(''));
            } else {
                //for (let ele in value) {
                //    if (this.rawCategories.indexOf(value[ele]) === -1) {
                //        callback(new Error(':' + value[ele]));
                //    }
                //}
                callback();
            }
        };
        return {
            finalCount: 1,
            category_bak: '',
            formCustom: {},
            oldWord: '',
            oldStatus: '',
            ruleCustom: {
                word: [{
                    validator: validateWord,
                    trigger: 'blur'
                }],
                status: [{
                    validator: validateStatus,
                    //trigger: 'blur,change'
                }],
                category: [{
                    validator: validateCategory,
                    trigger: 'blur,change'
                }]
            },
        }
    },
    props: ['basicWordItemShow', 'formCustom2', 'rawCategories'],
    computed: {
        ...mapState(['currentUserName', 'baseurl', 'currentComponent']),
        operation: function () {
            if (this.formCustom.uid === '') {
                return ''
            } else {
                return ''
            }
        }
    },
    beforeDestroy() {},
    mounted() {
        // console.log(this.formCustom2)
        if (JSON.stringify(this.formCustom2) !== "{}") {
            //this.$set(this.formCustom,'itemIndex',this.formCustom2.itemIndex)
            this.$set(this.formCustom, 'Items', this.formCustom2.Items)
            this.$set(this.formCustom, 'uid', this.formCustom2.uid)
            this.category_bak = this.formCustom2.Items[0].value.category.join(',')
            this.oldWord = JSON.parse(JSON.stringify(this.formCustom2.Items[0].value.word)) // word    
            this.oldStatus = JSON.parse(JSON.stringify(this.formCustom2.Items[0].value.status)) // word    
            //console.log(this.category_bak)
            // console.log(this.formCustom,'yyy')
            //    

            //if (this.formCustom.Items[0].value.source === '') {
            for (let ele in this.formCustom.Items) {
                //word
                let word = this.formCustom.Items[ele].value.word
                //console.log('wwwword', word)
                if (word) {
                    this.checkIfExist(word, ele)
                }
            }
            //}
        }
    },
    methods: {
        ...mapMutations(['changeUrlItemWindowShow']),
        searchWordStatus: function (xindex) {
            // console.log(this.formCustom.Items[xindex].value.word)
            // let checkWord = this.shownformCustomItems[xindex].value.word
            //          
            if (this.formCustom.Items[xindex].value.word.replace(/ /g, '')) { //  
                // this.formCustom.Items[xindex].value.status = ' ...'
                this.$set(this.formCustom.Items[xindex].value, 'status', ' ...')
                this.checkIfExist(this.formCustom.Items[xindex].value.word, xindex)
            } else {
                this.formCustom.Items[xindex].value.status = ''
            }

        },
        checkIfExist: function (target, xindex) {
            let self = this
            let Params = {
                'keyword': target,
                'fullMatch': true
            } //  
            self.axios({
                    method: 'get',
                    url: self.baseurl + 'StopDict/' + self.currentComponent,
                    params: Params
                })
                .then(res => {
                    // console.log('StopDict ...',res)
                    //  
                    if (res.data.count > 0) {
                        // 
                        // self.formCustom.Items[xindex].value.status = ' ' + '(' + res.data.content[0].modifiedTime + ')'
                        self.$set(self.formCustom.Items[xindex].value, 'status', ' ' + '(' + res.data.content[0].modifiedTime + ')')
                    } else {
                        //  StopDict   InvalidDict 
                        // console.log('not StopDict')
                        let Params = {
                            'keyword': target,
                            'fullMatch': true
                        }
                        self.axios({
                                method: 'get',
                                url: self.baseurl + 'InvalidDict/' + self.currentComponent,
                                params: Params
                            })
                            .then(res => {
                                // console.log('invalidDict ...',res)
                                if (res.data.count > 0) {
                                    //  
                                    //self.formCustom.Items[xindex].value.status = ' ' + '(' + res.data.content[0].modifiedTime + ')'
                                    self.$set(self.formCustom.Items[xindex].value, 'status', ' ' + '(' + res.data.content[0].modifiedTime + ')')
                                } else {
                                    //  ,   
                                    // console.log('not invalidDict')
                                    let Params = {
                                        'keyword': target,
                                        'fullMatch': true
                                    }
                                    self.axios({
                                            method: 'get',
                                            url: self.baseurl + 'UserDict/' + self.currentComponent,
                                            params: Params
                                        })
                                        .then(res => {
                                            // console.log('userDict ...',res)
                                            if (res.data.count > 0) {
                                                //  
                                                //self.formCustom.Items[xindex].value.status = ' ' + '(' + res.data.content[0].modifiedTime + ')'
                                                self.$set(self.formCustom.Items[xindex].value, 'status', ' ' + '(' + res.data.content[0].modifiedTime + ')')
                                            } else {
                                                //    
                                                // console.log('not userDict')
                                                let Params = {
                                                    'wordPart': target,
                                                    'fullMatch': true
                                                }
                                                self.axios({
                                                        method: 'get',
                                                        url: self.baseurl + 'basicWords/' + self.currentComponent,
                                                        params: Params
                                                    })
                                                    .then(res => {
                                                        // console.log('basicWords ...',res)
                                                        if (res.data.count > 0) {
                                                            //self.formCustom.Items[xindex].value.status = res.data.content[0].status  +  '(' + res.data.content[0].timestamp +')'
                                                            self.$set(self.formCustom.Items[xindex].value, 'status', res.data.content[0].status + '(' + res.data.content[0].timestamp + ')')

                                                        } else {
                                                            //   
                                                            // console.log('not basicWords')
                                                            // self.formCustom.Items[xindex].value.status = ''
                                                            self.$set(self.formCustom.Items[xindex].value, 'status', '')
                                                        }
                                                    })
                                                    .catch(err => {
                                                        console.log(err)
                                                    })
                                            }
                                        })
                                        .catch(err => {
                                            console.log(err)
                                        })
                                }
                            })
                            .catch(err => {
                                console.log(err)
                            })
                    }

                })
                .catch(err => {
                    console.log(err)
                    // self.$Message.error(err.response.data.detail);
                })

        },
        handleAdd: function () {
            //this.formCustom.itemIndex++
            this.formCustom.Items.push({
                value: {
                    'word': '',
                    'category': [],
                    'status': '...',
                    'source': ''
                },
                //index: this.formCustom.itemIndex
            });
            this.finalCount = 1

        },
        handleRemove: function (index) {
            //this.formCustom.Items[index].status = 0
            this.formCustom.Items.splice(index, 1)
            if (this.formCustom.Items.length === 0) {
                this.finalCount = 0
            }
        },
        modalOk: function (name) {
            let self = this
            // console.log('',self.formCustom.Items[0].value.word)
            // check  
            self.$refs[name].validate((valid) => {
                if (valid) {
                    if (self.formCustom.uid === '') {
                        console.log('')
                        //  ,   
                        // console.log(self.shownformCustomItems)
                        let result = []
                        for (let ele in self.formCustom.Items) {
                            // if (self.formCustom.Items[ele].status === 1 && self.formCustom.Items[ele].value.status === ''){
                            //if (self.formCustom.Items[ele].status === 1 ){

                            // let temp = self.formCustom.Items[ele].value

                            //     
                            // basicWords   :  
                            // if (self.formCustom.Items[0].value.source === ''){
                            //     console.log('')
                            //     
                            //     result.push({'word':self.formCustom.Items[ele].value.word, 'category':self.formCustom.Items[ele].value.category,'source':''}) 
                            // }else {
                            //console.log('')
                            result.push({
                                'word': self.formCustom.Items[ele].value.word,
                                'category': self.formCustom.Items[ele].value.category,
                                'source': self.formCustom.Items[ele].value.source
                            })
                            //}
                            //}
                        }
                        //    
                        //console.log('result',result)
                        if (result.length !== 0) {
                            //  
                            self.$emit('createBasicWords', result)
                            self.$refs[name].resetFields()
                            self.$emit('update:basicWordItemShow', false)
                        } else {
                            self.$Message.warning('!');
                            self.$emit('update:basicWordItemShow', false)
                        }
                    } else {
                        console.log('')
                        //console.log(self.formCustom)
                        //let result = 
                        // (   )
                        if (self.formCustom.Items[0].value.status.indexOf('') !== -1 || self.category_bak.split(',') !== self.formCustom.Items[0].value.category) {
                            let result = {
                                'word': self.formCustom.Items[0].value.word,
                                'category': self.formCustom.Items[0].value.category
                            }
                            self.$emit('UpdateBasicWords', {
                                'uid': self.formCustom.uid,
                                'data': result
                            })

                            //    
                            if (self.formCustom.Items[0].value.status.indexOf('') !== -1 && (self.oldStatus.indexOf('') !== -1 || self.oldStatus.indexOf('') !== -1)) {
                                if (self.oldStatus.indexOf('') !== -1) {
                                    //   
                                    self.axios({
                                            method: "patch",
                                            url: self.baseurl + "StopDict/" + self.currentComponent + "/" + self.oldWord,
                                            data: {
                                                'word': self.formCustom.Items[0].value.word,
                                                'operator': localStorage.getItem('kwmUser')
                                            },
                                            params: {
                                                'flag': 'word'
                                            }
                                        })
                                        .then((res) => {
                                            if (res.data == 'item exist') {
                                                self.$Message.info("!");
                                            } else {
                                                if (res.data.count !== "") {
                                                    self.itemCount = res.data.count;
                                                }
                                                //self.StopDictItemData = res.data.content;
                                                self.$Message.success("!");
                                            }
                                        })
                                        .catch((err) => {
                                            console.log(err);
                                            //self.$Message.error("!");
                                            self.$Message.error({
                                                content: JSON.stringify(err.response.data.detail),
                                                duration: 0,
                                                closable: true
                                            });
                                        });

                                } else if (self.oldStatus.indexOf('') !== -1) {
                                    //  
                                    self.axios({
                                            method: "patch",
                                            url: self.baseurl + "InvalidDict/" + self.currentComponent + "/" + self.oldWord,
                                            data: {
                                                'word': self.formCustom.Items[0].value.word,
                                                'operator': localStorage.getItem('kwmUser')
                                            },
                                            params: {
                                                'flag': 'word'
                                            }
                                        })
                                        .then((res) => {
                                            if (res.data == 'item exist') {
                                                self.$Message.info("!");
                                            } else {
                                                if (res.data.count !== "") {
                                                    self.itemCount = res.data.count;
                                                }
                                                //self.InvalidDictItemData = res.data.content;
                                                self.$Message.success("!");
                                            }
                                        })
                                        .catch((err) => {
                                            console.log(err);
                                            //self.$Message.error("!");
                                            self.$Message.error({
                                                content: JSON.stringify(err.response.data.detail),
                                                duration: 0,
                                                closable: true
                                            });
                                        });
                                }
                            }
                        } else {
                            self.$Message.info(',!');
                        }
                        self.$refs[name].resetFields()
                        self.$emit('update:basicWordItemShow', false)
                    }
                } else {
                    // console.log('')
                    self.$Message.error('');
                    // self.modal1 = true
                }
            })
        },
        modalCancel: function (name) {
            this.$refs[name].resetFields()
            this.$emit('update:basicWordItemShow', false)

        },
        modalDelete: function (uid) {
            //   
            // console.log('uid',uid)
            this.$emit('deletebasicWordItem', {
                'uid': uid
            })
            this.$emit('update:basicWordItemShow', false)
        }
    }
}
</script>

<style scoped>
.basicWords-itemPage {
    height: 500px;
    max-height: 550px;
    overflow: auto
}

.basicWords-itemPage .basicWords-itemPage-form-main {
    border: 1px dashed #81b165;
    border-radius: 5px;
    padding: 10px;
    margin-bottom: 10px;
}

.basicWords-itemPage-form-row {
    margin: 3px 0px;

}

.basicWords-itemPage-form-main-word {
    display: flex
}

.xyz {
    border: none !important
}

>>>.status .ivu-input.ivu-input-default {
    border: none !important
}

>>>.status .ivu-input.ivu-input-default:focus {
    outline: none;
    box-shadow: 0 0 0 0px
}

.basicWords-itemPage-form-main-status-delete {
    margin-left: 5px
}

.status.ivu-form-item,
.source.ivu-form-item {
    margin-bottom: 20px
}
</style>
